import React, { useState, useEffect, useRef } from 'react'
import './ContactBar.scss'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom';

const ContactBar = () => {
    // Store (chat Reducer)
    const storeClientInfo = useSelector(state => state.chat.clientInfo);
    // State()
    const [salerInfo, setSalerInfo] = useState({
        name: '',
        phone: '',
        portraitUrl: ''
    })
    const [musicIsOpen, setMusicIsOpen] = useState(true);
    const audioDOM = useRef();

    useEffect(() => {
        console.log('ContactBarstoreClientInfo');
        console.log(storeClientInfo);
        console.log('ContactBarstoreClientInfo');

        // storeclientstate salerInfo
        if (storeClientInfo) {
            // if (storeClientInfo.nickname && storeClientInfo.phone && storeClientInfo.logo) {
            setSalerInfo({
                ...salerInfo,
                name: storeClientInfo.nickname,
                phone: storeClientInfo.phone,
                portraitUrl: storeClientInfo.logo
            })
        }
    }, [storeClientInfo])

    // 
    const handleMusicIconClick = () => {
        if (musicIsOpen) {
            audioDOM.current.pause();
        } else if (!musicIsOpen) {
            audioDOM.current.play();
        }
        setMusicIsOpen(!musicIsOpen);
    }

    return (
        <div className="ContactBar">

            <audio src={require('../../images/BgMusic.mp3')} ref={audioDOM} autoPlay loop></audio>

            {/*  */}
            <div className="leftContainer">
                {/*  */}
                <div className="portraitContainer">
                    <img src={(salerInfo.portraitUrl) ? (salerInfo.portraitUrl) : (require('../../images/Portrait.png'))} alt="" />
                </div>
                {/*  */}
                <div className="infoContainer">
                    <div className="central">
                        <div className="name">{(salerInfo.name) ? (salerInfo.name) : ('')}</div>
                        <div className="phone">{(salerInfo.phone) ? (salerInfo.phone) : ('')}</div>
                    </div>
                </div>
            </div>

            {/*  */}
            <div className="rightContainer">
                {/*  */}
                <div className="iconContainer">
                    {/*  */}
                    <div className="phone">
                        <img src={require('../../images/ContactBar/PhoneIcon.png')} alt="" />
                    </div>
                    <div className="messanger">
                        {(window.enableBackEndFunction) ? (<Link to='/GreenLand/CustomerChatPage'></Link>) : (null)}
                        <img src={require('../../images/ContactBar/MessageIcon.png')} alt="" />
                    </div>
                    <div className={"music" + ((musicIsOpen) ? "" : " musicIsNotActive")} onClick={() => handleMusicIconClick()}>
                        <img className='icon notMute' src={require('../../images/ContactBar/MusicIsOpen.png')} alt="" />
                        <svg className='icon mute' t={1568962920823} viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id={9382}><path d="M469.726 139.266v0l-217.47 163.104h-135.985c-37.804 0-67.889 30.141-67.889 67.327v273.082c0 36.683 30.397 67.327 67.889 67.327h135.985l217.47 163.104c30.237 22.675 54.353 10.267 54.353-27.359v-679.232c0-37.152-24.338-49.873-54.353-27.359z" p-id={9383} /><path d="M887.187 538.378l69.126-69.126c22.907-22.907 22.907-60.044 0-82.958v0c-22.907-22.907-60.044-22.907-82.958 0l-69.126 69.126-69.126-69.126c-22.907-22.907-60.044-22.907-82.958 0s-22.907 60.044 0 82.958l69.126 69.126-69.126 69.126c-22.907 22.907-22.907 60.044 0 82.958v0c22.907 22.907 60.044 22.907 82.958 0l69.126-69.126 69.126 69.126c22.907 22.907 60.044 22.907 82.958 0v0c22.907-22.907 22.907-60.044 0-82.958l-69.126-69.126z" p-id={9384} /></svg>
                    </div>
                    <div className="backEnd">
                    {(window.enableBackEndFunction) ? (<Link to='/GreenLand/Backend/HomePage'></Link>) : (null)}
                        <img src={require('../../images/ContactBar/BackEndIcon.png')} alt="" />
                    </div>
                </div>
            </div>

        </div>
    )
}

export default ContactBar;