const initialState = {
    currentPageInNavigationLevel: "HomePage",    // ()
}

export default function defaultReducers(state = initialState, action) {
    switch(action.type) {
        //  ()
        case 'CHANGECURRENTPAGEINNAVIGATIONLEVEL':
            return {
                ...state,
                currentPageInNavigationLevel: action.value,
            }
        default:
            return state
    }
}