import request from '../request'

let pid = window.pid;

// 
export function login(data) {
  return request({
    url: '/login/login',
    method: 'POST',
    data: data
  })
}
// socket bind
export function bind(data) {
  return request({
    url: '/api/bind ',
    method: 'POST',
    data: data
  })
}
